body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.search-box {
  padding: 20px 0; }
  .search-box form {
    display: -webkit-flex;
    display: flex; }
  .search-box input {
    border: 1px solid #999;
    color: #444;
    border-radius: .2rem;
    padding: 10px;
    width: 100%;
    display: inline-block;
    margin-right: .5rem; }
    .search-box input[type="submit"] {
      background-color: #1890ff;
      text-transform: uppercase;
      color: #ffffff;
      border-color: #1890ff;
      cursor: pointer;
      -webkit-transition: .1s;
      transition: .1s;
      padding: .5rem;
      margin-left: .5rem;
      margin-right: 0;
      width: 10%; }

* {
  margin: 0 auto;
  padding: 0;
  text-decoration: none;
  border: 0;
  list-style: none; }

.home {
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto; }
  .home h3 {
    font-size: 36px;
    color: #444; }

.List {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 10px 0; }

.User {
  padding: 10px 0;
  border-bottom: 1px solid #eaeaea;
  width: 100%;
  color: #444;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap; }
  .User .avatar {
    width: 30px;
    height: 30px;
    margin: 0; }
  .User .informations {
    padding-left: 10px;
    margin: 0; }
    .User .informations p {
      color: #999; }
  .User img {
    width: 60px;
    height: 60px;
    border-radius: 60px; }

.Loading {
  padding: 20px 0; }

.Empty {
  padding: 20px 0; }

.Item {
  padding: 10px 0;
  border-bottom: 0.1rem solid #eaeaea;
  width: 100%; }
  .Item strong {
    font-size: 18px;
    font-weight: 500;
    margin: 0;
    color: #444; }
  .Item p {
    font-size: 14px;
    padding-top: 5px;
    color: #999; }

* {
  padding: 0;
  text-decoration: none;
  border: 0;
  list-style: none; }

